import axios from 'axios';
import VueAxios from 'vue-axios';
import $ from 'jquery';
import Bootstrap from 'bootstrap';
//  fontawesome 
import '@fortawesome/fontawesome-free/css/all.css';
import '@fortawesome/fontawesome-free/js/all.js';
//  fontawesome 
//  vue2-datepicker 
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
//  vue2-datepicker 
//  vue2-leaflet  
import * as Vue2Leaflet from 'vue2-leaflet';
import 'leaflet/dist/leaflet.css';
//  vue2-leaflet  
import Vue from 'vue';
//  vee-validate 
import { ValidationObserver, ValidationProvider, extend, localize, configure } from 'vee-validate';
import zhTW from 'vee-validate/dist/locale/zh_TW.json';
import * as rules from 'vee-validate/dist/rules';
//  vee-validate 
import App from './App';
import router from './router';
//  cloudinary-vue 
import Cloudinary, { CldImage, CldTransformation } from "cloudinary-vue";
//  cloudinary-vue 
//  HTML  vue2-editor 
import { VueEditor } from "vue2-editor";
//  HTML  vue2-editor 
// 
import GlobalVariables from '@/library/variables.vue';
// 
// 
import GlobalFunctions from '@/library/functions.vue';
// 

//  jQuery
window.$ = $;

Vue.config.productionTip = false;

// 
Vue.use(VueAxios, axios, $, Bootstrap, DatePicker, Vue2Leaflet, Cloudinary, {
  configuration: { cloudName: "demo" },
  components: {
    CldImage,
    CldTransformation
  }
}, VueEditor);

//  eventBus
Vue.prototype.$eventBus = new Vue();
// 
Vue.prototype.GlobalVariables = GlobalVariables;
Vue.prototype.GlobalFunctions = GlobalFunctions;
//  Date  input 
Date.prototype.Format = GlobalFunctions.format;

//  vee-validate 
Object.keys(rules).forEach((rule) => {
  extend(rule, rules[rule]);
});

//  vee-validate 
localize('zh_TW', zhTW);

//  vee-validate  input  form 
Vue.component('ValidationObserver', ValidationObserver);
Vue.component('ValidationProvider', ValidationProvider);

//  vee-validate 
configure({
  events: 'input|blur|change',
  classes: {
    valid: 'is-valid',
    invalid: 'is-invalid'
  }
});

// 
Vue.filter("currency", function (n) {
  return Number(n).toFixed(0).replace(/./g, function (c, i, a) {
    return i && c !== "." && (a.length - i) % 3 === 0 ? "," + c : c;
  });
});
Vue.filter("dollarSign", function (n) {
  return `$ ${n}`;
});
// 
Vue.filter("memberStatus", function (n) {
  switch (n) {
    case '-1' || -1:
      return '';
    case '0' || 0:
      return '';
    case '1' || 1:
      return '';
    case '2' || 2:
      return '';
  }
});
Vue.filter("orderStatus", function (n) {
  switch (n) {
    case '-1' || -1:
      return '';
    case '0' || 0:
      return '';
    case '1' || 1:
      return '';
    case '2' || 2:
      return '';
  }
});
Vue.filter("orderDetailStatus", function (n) {
  switch (n) {
    case '-1' || -1:
      return '';
    case '0' || 0:
      return '';
    case '1' || 1:
      return '';
    case '2' || 2:
      return '';
  }
});
Vue.filter("projectStatus", function (n) {
  switch (n) {
    case '-1' || -1:
      return '';
    case '0' || 0:
      return '';
    case '1' || 1:
      return '';
  }
});

//  Vue 
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
});


