<template>
  <section id="project-searching-page" class="position-relative">
    <!--  -->
    <div class="row">
      <div class="col-10">
        <div class="form-row">
          <div class="form-group col-md-3">
            <label for="project-id"></label>
            <input
              type="text"
              class="form-control"
              id="project-id"
              placeholder="PJ0000001"
              v-model="queryData.projectID"
            />
          </div>
          <div class="form-group col-md-4">
            <label for="project-name"></label>
            <input
              type="text"
              class="form-control"
              id="project-name"
              placeholder=""
              v-model="queryData.projectName"
            />
          </div>
          <div class="form-group col-md-3">
            <label for="project-status"></label>
            <select
              id="project-status"
              class="form-control form-select-lg"
              v-model="queryData.projectStatus"
            >
              <option disabled selected value></option>
              <option value="%%"></option>
              <option value="1"></option>
              <option value="0"></option>
              <option value="-1"></option>
            </select>
          </div>
        </div>
      </div>
      <div id="rechoose-mode-link" class="col-2 d-flex justify-content-end">
        <a href="" @click.prevent="$router.push('/Admin/Projects-Manager')"
          >
          <i class="fas fa-sign-out-alt"></i>
        </a>
      </div>
    </div>
    <div class="row">
      <div class="col-10">
        <div class="form-row">
          <div class="form-group col-md-3">
            <label for="project-category"></label>
            <select
              id="project-category"
              class="form-control form-select-lg"
              v-model="queryData.projectCategory"
            >
              <option disabled selected value></option>
              <option value="%%"></option>
              <option
                v-for="category in categoryList"
                :key="category['CATEGORY_NAME']"
                :value="category['CATEGORY_NAME']"
              >
                {{ category["CATEGORY_NAME"] }}
              </option>
            </select>
          </div>
          <div class="form-group col-md-4">
            <label for="project-departure-location"></label>
            <input
              type="text"
              class="form-control"
              id="project-departure-location"
              placeholder=""
              v-model="queryData.projectDepartureLocation"
            />
          </div>
          <div class="form-group col-md-3">
            <label for="project-price-beyond" class="form-label"
              ><span class="remarks"></span></label
            >
            <div class="h-50 px-1 d-flex flex-column align-items-start">
              <div class="project-price-str">
                {{
                  Number(queryData.projectPriceBeyond) | currency | dollarSign
                }}
              </div>
              <input
                type="range"
                class="form-range"
                min="0"
                max="5000"
                step="500"
                id="project-price-beyond"
                v-model="queryData.projectPriceBeyond"
              />
            </div>
          </div>
          <div class="form-group col-md-1 ml-3">
            <label for="">&nbsp;</label>
            <input
              type="button"
              class="btn btn-primary"
              value=""
              @click.prevent="submitProjectsQuery"
            />
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <!--  -->
    <div class="row">
      <div class="col-12">
        <h6 class="my-4"></h6>
        <table class="table table-striped queryResultsTable">
          <thead>
            <tr>
              <th class="text-center" scope="col"></th>
              <th class="text-center" scope="col"></th>
              <th class="text-center" scope="col"></th>
              <th class="text-center" scope="col"></th>
              <th class="text-center" scope="col"></th>
              <th class="text-center" scope="col"></th>
              <th class="text-center" scope="col"></th>
            </tr>
          </thead>
          <tbody v-for="(order, index) in currentPageContentArr" :key="index">
            <tr
              class="order-item"
              @click.prevent="
                checkProjectDetails(currentPageContentArr[index]['PROJECT_ID'])
              "
            >
              <th class="text-center" scope="row">
                {{ currentPageContentSerial[index] }}
              </th>
              <td>{{ currentPageContentArr[index]["PROJECT_ID"] }}</td>
              <td class="text-center">
                {{ currentPageContentArr[index]["PROJECT_NAME"] }}
              </td>
              <td class="text-center">
                {{ currentPageContentArr[index]["CATEGORY_NAME"] }}
              </td>
              <td class="text-center">
                {{
                  currentPageContentArr[index][
                    "PROJECT_ORIGINAL_PRICE_PER_PERSON"
                  ]
                    | currency
                    | dollarSign
                }}
              </td>
              <td class="text-center">
                {{ currentPageContentArr[index]["LOCATION_NAME"] }}
              </td>
              <td class="text-center">
                {{
                  currentPageContentArr[index]["PROJECT_STATUS"] | projectStatus
                }}
              </td>
            </tr>
          </tbody>
        </table>
        <div
          class="text-danger mt-5 text-center"
          v-if="currentPageContentArr.length <= 0"
        >
          
        </div>
      </div>
    </div>
    <!--  -->
    <div
      id="pagination-container"
      class="d-flex justify-content-center mt-4 position-absolute"
    >
      <Pagination
        v-show="currentPageContentArr.length > 0"
        :allContentArr="allProjectsArr"
        :itemsNumPerPage="itemsNumPerPage"
        @emitCurrentContentAndSerial="getCurrentContentAndSerial"
      ></Pagination>
    </div>
  </section>
</template>

<script>
// 
import Breadcrumb from "@/components/pages/sub-components/Breadcrumb";
// 
import Pagination from "@/components/pages/sub-components/Pagination";

export default {
  data() {
    return {
      currentMode: "",
      breadCrumbData: {
        pagesArr: ["", ""],
        currentPage: 2,
      },
      queryData: {
        projectID: "",
        projectName: "",
        projectStatus: "",
        projectCategory: "",
        projectDepartureLocation: "",
        projectPriceBeyond: 5000,
      },
      categoryList: [],
      allProjectsArr: [],
      itemsNumPerPage: 5,
      currentPageContentArr: [],
      currentPageContentSerial: [],
    };
  },
  components: { Breadcrumb, Pagination },
  created() {
    const categoryListAPI = `${process.env.REMOTE_HOST_PATH}/API/Forestage/QueryCategoryList.php`;
    const vm = this;

    this.$http.get(categoryListAPI).then((response) => {
      vm.categoryList = response.data;
    });
  },
  methods: {
    // 
    submitProjectsQuery() {
      const QueryProjectsAPI = `${process.env.REMOTE_HOST_PATH}/API/Backstage/QueryProjects.php`;
      const vm = this;

      this.$http
        .post(QueryProjectsAPI, JSON.stringify(vm.queryData))
        .then((response) => {
          vm.allProjectsArr = response.data;

          vm.allProjectsArr.sort((a, b) => {
            return a["SORT_INDEX"] - b["SORT_INDEX"];
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },
    // 
    getCurrentContentAndSerial(arr, num) {
      this.currentPageContentArr = arr;
      this.currentPageContentSerial = num;
    },
    // 
    checkProjectDetails(manageProjectID) {
      localStorage.removeItem("managingProject");
      localStorage.setItem("managingProject", manageProjectID);
      this.$router.push({ name: "" });
    },
  },
};
</script>

<style lang="scss" scoped>
section {
  height: 700px;
  h5 {
    span {
      font-size: 12px;
      color: green;
    }
  }
  #rechoose-mode-link {
    a {
      color: black;
      &:hover {
        opacity: 0.7;
      }
    }
  }
  .remarks {
    font-size: 12px;
  }
  .project-price-str {
    font-size: 12px;
    font-weight: 600;
    margin-top: -2px;
  }
  .queryResultsTable {
    tr {
      th {
        font-size: 14px;
      }
      td {
        font-size: 12px;
      }
    }
    .order-item {
      cursor: pointer;
    }
  }
  #pagination-container {
    transform: translate3d(-50%, -50%, 0);
    left: 50%;
    bottom: 8%;
  }
}
</style>


